.styble-spacing {
    &-part-2 {
        display: flex;
        label{
          font-size: 11px;
          display: inline-block;
          color: #8C8F94;
          margin-top: 4px;  
        }
        input {
            width: 100%;
            padding: 0;
            text-align: center;
            border-radius: 0;
            border-right: 0;
            border-color: #DDD;
            &:focus:not(:disabled){
                outline: none;
                box-shadow: none;
                border-color: #DDD;
            }
        }
    }
    &-all {
        width: 140px;
        height: 30px;
        button{
            width: 100%;
            height: 100%;
            padding: 0;
            border-radius: 0;
            justify-content: center;
            border: 1px solid #DDD;
            &.active{
                background-color: #008DB4;
                border-color: transparent;
            }
            &:focus:not(:disabled){
                outline: none;
                box-shadow: none;
            }
        }
    }
    &-left.box input,
    &-right.box input{
        border-right: 1px solid #DDD;
        margin-right: 0;
    }
}