.styble-tab-panel{
    .styble-button-group {
        width: 100%;
        &-list {
            display: flex;
            width: 100%;
        }
        &-list {
            border: 1px solid #D7DADE;
            button {
                padding: 0px;
                background-color: #ffffff;
                color: #1E1E1E;
                box-shadow: none;
                width: 100%;
                height: 32px;
                justify-content: center;
                &:focus:not(:disabled){
                    outline: none;
                    box-shadow: none;
                }
                span {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
        }
        &-list {
            button.active {
                span{
                    background-color: #008DB4;
                    color: #fff;
                    width: calc(100% - 4px);
                    height: 85%;
                    margin-top: 1px;
                    svg{
                        fill: #fff;
                    }
                }
            }
        }
        .has-border{
            button{
                border-left: 1px solid #D7DADE;
                &:first-child {
                    border-left: 0;
                }
                margin-left: 0;
            }
        }
    }
    .styble-button-group.button-style-2{
        justify-content: space-between;
        align-items: center;
        .styble-button-group-list {
            width: 130px;
        }
        .styble-component-title,
        .styble-component-top {
            margin-bottom: 0;
        }
    }
}