.styble-tab-panel{
    .styble-button-group {
        width: 100%;
        &-list {
            display: flex;
            width: 100%;
        }
        &-list {
            border: 1px solid #D7DADE;
            button {
                padding: 0px;
                background-color: #ffffff;
                color: #1E1E1E;
                box-shadow: none;
                width: 100%;
                height: 32px;
                justify-content: center;
                position: relative;
                &:focus:not(:disabled){
                    outline: none;
                    box-shadow: none;
                }
                span {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
                p {
                    opacity: 0;
                    visibility: hidden;
                    position: absolute;
                    top: -40px;
                    right: 50%;
                    transform: translateX(50%);
                    background-color: #757575;
                    color: #fff;
                    padding: 8px 10px;
                    white-space: nowrap;
                    font-size: 10px;
                    border-radius: 4px;
                    transition: .3s;
                }
                & p::after{
                    position: absolute;
                    content: "";
                    top: 30px;
                    right: 50%;
                    transform: translateX(50%);
                    border-width: 10px;
                    border-color: #757575 transparent transparent transparent;
                    border-style: solid;
                    opacity: 0;
                    visibility: hidden;
                    transition: .3s;
                }
            }
            button:hover p {
                opacity: 1;
                visibility: visible;
            }
            button:hover p::after {
                opacity: 1;
                visibility: visible;
            }
        }
        &-list {
            button.active {
                span{
                    background-color: #008DB4;
                    color: #fff;
                    width: calc(100% - 4px);
                    height: 85%;
                    margin-top: 1px;
                    svg{
                        fill: #fff;
                    }
                }
            }
        }
        .has-border{
            button{
                border-left: 1px solid #D7DADE;
                &:first-child {
                    border-left: 0;
                }
                margin-left: 0;
            }
        }
    }
    .styble-button-group.button-style-2{
        justify-content: space-between;
        align-items: center;
        .styble-button-group-list {
            width: 130px;
        }
        .styble-component-title,
        .styble-component-top {
            margin-bottom: 0;
        }
    }
}