.styble-media-control__wrapper {
    height: 130px;
    border-radius: 3px;
    border: 1px solid #E6E7E9;
    position: relative;
    background-color: #fff;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-display: flex;
    -moz-display: flex;
    -ms-display: flex;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    .styble-media-video {
        position: absolute;
        top: 42%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 45px;
        color: #2f8db4; 
    }
}

.styble-media-control__clickable {
    cursor: pointer;
    border: unset;
    color: transparent;
    background-color: transparent;
}

.styble-media-control__button {
    width: 18px;
    height: 18px;
    -webkit-display: flex;
    -moz-display: flex;
    -ms-display: flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}

.styble-media-control__button--add {
    background-color: #ccc;
}

.styble-media-control__clickable--add {
    width: 100%;
    height: 100%;
    -webkit-display: flex;
    -moz-display: flex;
    -ms-display: flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    justify-content: center;
}
.styble-media-control__clickable--replace {
    width: 100%;
    height: 25px;
    color: #50575E;
    background-color: #F0F0F1;
    position: absolute;
    bottom: -1px;
    -webkit-display: flex;
    -moz-display: flex;
    -ms-display: flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    justify-content: center;
}
.styble-media-control__button--close {
    background-color: #F0F0F1;
    position: absolute;
    top: 0;
    right: 0;
}

.styble-media-control__clickable--add span,
.styble-media-control__clickable--close span {
    color: #50575E;
    font-size: 18px;
    font-weight: 300;
}
.styble-infobox .styble-infobox-num,
.styble-infobox .styble-infobox-icon {
    line-height: 0;
}
.styble-infobox .styble-infobox-image{
    flex-shrink: 0;
}