/* Color controls. */
.styble-color-picker {
    display: flex;
	justify-content: space-between;
    align-items: center;
	margin-bottom: 10px;
	.styble-component-title{
		margin-bottom: 0 !important;
	}
	p {
		margin-bottom: 0;
	}
	.components-dropdown,
	button.components-button {
		padding: 0;
		height: 30px;
		:focus:not(:disabled) {
			box-shadow: none;
			outline: none;
		}
		span.component-color-indicator {
			width: 30px;
			height: 30px;
		}
	}
	&-palette{
		display: flex;
		margin-left: 15px;
		flex-wrap: wrap;
		gap: 8px;
		margin-bottom: 8px;
		li{
			border-radius: 100%;
			height: 30px;
			width: 30px;
			margin: 0;
			button{
				width: 100%;
				padding: 0;
				&:focus:not(:disabled) {
					box-shadow: none;
					outline: none;
				}
			}
		}
	}
}